import React, { Component } from 'react';
import {withRouter} from 'react-router-dom'
import { Icon, Flex,TabBar, Button} from 'antd-mobile';
class List1 extends Component{
    constructor(props){
        super(props);
    }
    render(){
        return <div>
            <Icon type="left" size="lg" style={{position:"absolute"}} onClick={()=>{this.props.history.push('/home')}}/>
            <Flex style={{top:"0px"}}>
                <img src={require('../images/books.jpg')}/>
            </Flex>
            <div style={{height:"120px"}}></div>
            
            <Flex style={{height:"100%",bottom:"0px"}}>
                <Flex.Item>
                    <TabBar
                    unselectedTintColor="#949494"
                    tintColor="grey"
                    barTintColor="white"
                    tabBarPosition="bottom"
                    >
                        <TabBar.Item
                        title="店铺"
                        key="store"
                        icon={
                            <i className='iconfont icon-dianpu1'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-dianpu1-copy'></i>                           
                        }
                        data-seed="logId"
                        >
                        </TabBar.Item>
                        <TabBar.Item
                        icon={
                            <i className='iconfont icon-kefu'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-kefu-copy'></i>                           
                        }
                        title="收藏"
                        key="save"
                        data-seed="logId1"
                        >
                        </TabBar.Item>
                        <TabBar.Item
                        icon={
                            <i className='iconfont icon-shoucang1'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-shoucang1-copy'></i>  
                        }
                        title="客服"
                        key="cs"
                        >
                        
			            </TabBar.Item>
                    </TabBar>
                </Flex.Item>
                <Flex.Item style={{margin:0}}>
                    <Button style={{borderRadius:0,backgroundColor:"orange"}}type="warning">加入购物车</Button>
                </Flex.Item>
                <Flex.Item style={{margin:0}}>
                    <Button style={{borderRadius:0,backgroundColor:"red"}} type="warning">立即购买</Button>
                </Flex.Item>
            </Flex>
            
            
        </div>
    }
}
export default withRouter(List1);